.bbox{
  width: 437px;
  height: 110px;
  ul{
    width: 100%;
    height: 86px;
    margin: 0;
    padding: 0;
    li{
      margin-right: 15px;
      float: left;
      list-style: none;
      width: 70px;
      height: 77px;
      text-align: center;
      span{
        display: inline-block;
        width: 61px;
        height: 53px;
      }
      p{
        color: #ffffff;
        font-size: 14px;
        margin-bottom: 0;
      }
    }
  }
  .sbox{
    width: 437px;
    height: 24px;
    ul{
      width: 100%;
      height: 100%;
      li{
        margin-right: 15px;
        width: 70px;
        height: 24px;
        background-color: #00a5ff;
	      border-radius: 4px;
        opacity: 0.6;
        text-align: center;
        color: #ffffff;
        font-size: 14px;
      }
    }
  }
}